@import url(https://fonts.googleapis.com/css?family=Open+Sans:400,400i,700);
.App {
  text-align: center;
}




body {
  font-family: roboto; }

body {
  font-size: 10px; }

h1 {
  font-size: 4.6rem;
  line-height: 5.1rem; }

h2 {
  font-size: 2.8rem; }

h3 {
  font-size: 2.3rem; }

p,
article a,
.navbar,
button,
input {
  font-size: 1.5rem;
  line-height: 2rem; }

* {
  box-sizing: border-box; }

button {
  padding: 0;
  border: 0;
  outline: 0;
  cursor: pointer;
  background: none; }

input {
  outline: 0; }

a {
  text-decoration: none; }

.capitalize {
  text-transform: capitalize; }

.modal-content h4 {
  text-align: center; }

.modal-content .auth-container {
  width: 40vw;
  margin: 40px auto; }

.modal-content .google-auth {
  width: 100%;
  text-decoration: none; }

.modal-content a {
  color: white; }

.timeline {
  width: 70vw;
  margin: 0 auto; }
  .timeline body {
    font-family: "Open Sans", sans-serif;
    margin: 0;
    padding: 0 4em; }
  .timeline main {
    min-width: 300px;
    max-width: 400px;
    margin: auto; }
  .timeline li {
    font-size: 1em;
    line-height: 1.5em;
    border-top: 3px solid;
    -webkit-border-image: -webkit-gradient(linear, left top, right top, from(#FA255E), to(#C39EA0));
    -webkit-border-image: linear-gradient(to right, #FA255E 0%, #C39EA0 100%);
            border-image: -webkit-gradient(linear, left top, right top, from(#FA255E), to(#C39EA0));
            border-image: linear-gradient(to right, #FA255E 0%, #C39EA0 100%);
    border-image-slice: 1;
    border-width: 3px;
    margin: 0;
    padding: 40px;
    counter-increment: section;
    position: relative;
    color: black; }
  .timeline .li-l {
    text-align: left; }
  .timeline .li-r {
    text-align: right; }
  .timeline li:nth-child(odd) {
    border-right: 3px solid;
    padding-left: 0; }
    .timeline li:nth-child(odd):before {
      left: 100%;
      margin-left: -20px; }
  .timeline li:nth-child(even) {
    border-left: 3px solid;
    padding-right: 0; }
    .timeline li:nth-child(even):before {
      right: 100%;
      margin-right: -20px; }
  .timeline li:first-child {
    border-top: 0;
    border-top-right-radius: 0;
    border-top-left-radius: 0; }
  .timeline li:last-child {
    border-bottom-right-radius: 0;
    border-bottom-left-radius: 0; }
  .timeline i {
    color: #FA255E; }

.create-bid-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center; }
  .create-bid-container label {
    font-size: 1.4rem;
    margin-bottom: 0.7rem; }

nav {
  background-color: #F8E5E5; }
  nav .brand-logo {
    color: black;
    margin-left: 3%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 120px;
    left: 0;
    -webkit-transform: translateX(0);
    transform: translateX(0); }
  nav a {
    color: black; }
  nav .logo-img {
    height: 40px; }
  nav .profile-image {
    border-radius: 50%;
    height: 40px; }
  nav .sidenav-trigger {
    float: right; }
  nav .carousel {
    overflow: visible; }

.home {
  margin-bottom: 50px; }
  .home .title-container {
    height: 500px; }
  .home h1 {
    font-size: 3rem;
    margin: 0 20px 20px 20px;
    padding-top: 50px;
    margin-right: 100px;
    font-weight: 500;
    line-height: 3rem; }
  .home .title-container {
    background: -webkit-gradient(linear, left top, left bottom, from(#f8e5e5), color-stop(54%, #c39ea0), to(#fa255e));
    background: linear-gradient(to bottom, #f8e5e5 0%, #c39ea0 54%, #fa255e 100%); }
    .home .title-container img {
      margin-left: 35vw; }
  .home .mudo-container {
